import React from 'react';
import * as dayjs from 'dayjs';
import { TableCell, TableBody, TableRow } from '@windmill/react-ui';
import Status from '../table/Status';

const OrderTable = ({ orders }) => {
  return (
    <>
      <TableBody>
        {orders?.map((order) => (
          <TableRow key={order._id}>
            <TableCell>
              <span className="text-sm">
                {dayjs(order.created_at).format('MMM D, YYYY')}
              </span>
            </TableCell>

            <TableCell>
              <span className="text-sm ">{order.customerDetails.address.district}</span>
            </TableCell>
            <TableCell>
              <div>{order.customerDetails.name}</div>
              {' '}
              <div className="text-sm">{order.customerDetails.email}</div>{' '}
              <div className="text-sm">{order.customerDetails.phoneno}</div>{' '}
            </TableCell>
            <TableCell>
              <span className="text-sm font-semibold">
                {order.product_name}
              </span>
            </TableCell>
            <TableCell>
              <span className="text-sm font-semibold">
                {order.payment_method}
              </span>
            </TableCell>
            <TableCell>
              {' '}
              <span className="text-sm font-semibold">
                &#8377; {Math.round(order.total_amount)}.00
              </span>{' '}
            </TableCell>
            <TableCell>
              <Status status={order.status} />
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default OrderTable;
